/**
 * WordPress dependencies
 */
import { _x } from '@safe-wordpress/i18n';

/**
 * External dependencies
 */
import { ConversionActionScopeView, HelpIcon } from '@nab/components';
import type { CAViewProps, ConversionActionScope } from '@nab/types';

/**
 * Internal dependencies
 */
import type { Attributes } from './types';

export const View = ( props: CAViewProps< Attributes > ): JSX.Element => (
	<>
		<div>
			<ActualView { ...props } />
		</div>
		<ConversionActionScopeView scope={ props.scope } />
	</>
);

const ActualView = ( { scope }: CAViewProps< Attributes > ): JSX.Element => {
	const label = useLabel( scope );
	const help = useHelp();
	return (
		<>
			{ label }
			<HelpIcon text={ help } />
		</>
	);
};

// =====
// HOOKS
// =====

const useLabel = ( scope: ConversionActionScope ): string => {
	switch ( scope.type ) {
		case 'all-pages':
			return _x(
				'A visitor engages with any page.',
				'text',
				'nelio-ab-testing'
			);

		case 'test-scope':
			return _x(
				'A visitor engages with a tested page.',
				'text',
				'nelio-ab-testing'
			);

		case 'urls':
		case 'post-ids':
		case 'php-function':
			return _x(
				'A visitor engages with certain pages.',
				'text',
				'nelio-ab-testing'
			);
	}
};

const useHelp = (): string => {
	return _x(
		'A visitor is considered engaged if they stay on the targeted page for 10 seconds or more, scroll halfway down, or click on something (like a link or button).',
		'text',
		'nelio-ab-testing'
	);
};
